.laptopinfo {
  background: #ffff;
  margin: 10px auto;
  display: flex;
  justify-content: center;
  max-width: 1026px;
  zoom: 1.3;
  border-radius: 30px;
}

.container {
  zoom: 1.3;
}

.uploadimage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 300px;
  background-color: #F6F6F6;
  margin: 0 auto;
  border: 2px dashed #4386A9;
  border-radius: 18px;

}


.uploadimage p {
  font-size: 17px;
}

.uploadimage input {
  color: #fff;
  float: right;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 176px;
  height: 40px;
  background: #62A1EB;
  cursor: pointer;
  border: none;


}

.laptopnameandbrand {
  width: 100%;
  display: flex;
  align-items: center;

}

.top {

  padding-top: 4rem;
}

.uploadimage p {
  color: #4386A9;
}

.lnamep {
  font-family: Helvetica Neue;
  font-size: 14px;
  font-weight: 800;
  line-height: 21px;
  letter-spacing: 0em;
  text-align: left;

}

.laptopname {
  width: 100%;
}

.laptopname input {
  width: 80%;
  height: 30px;
  border-radius: 3px;
}

.laptopbrand {
  width: 90%;

}


.holder {
  font-size: 8px;
  transform: translateY(-70%);
}



.top::after {
  content: "";
  position: absolute;

  transition: .5s ease;
  width: 10px;
}

.mid-top {
  display: flex;
  justify-content: flex-start;
}

.mid-mid {
  display: flex;
}

.mid-bot-right {}

input[type=radio] {
  background: pink;
}

.bot-top-top {
  display: flex;
}

input[type="date"]::-webkit-datetime-edit,
input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-clear-button {
  color: #fff;
  position: relative;
}

input[type="date"]::-webkit-datetime-edit-year-field {
  position: absolute !important;
  border-left: 1px solid #8c8c8c;
  padding: 2px;
  color: #000;
  left: 56px;
}

input[type="date"]::-webkit-datetime-edit-month-field {
  position: absolute !important;
  border-left: 1px solid #8c8c8c;
  padding: 2px;
  color: #000;
  left: 26px;
}


input[type="date"]::-webkit-datetime-edit-day-field {
  position: absolute !important;
  color: #000;
  padding: 2px;
  left: 4px;
}

.lariicon {
  float: right;
  transform: translate(-270%, -170%);
  zoom: 0.7;

}

input[type='radio']::after {
  content: "";
  height: 10px;
  width: 10px;
  background: transparent;
  border-radius: 50%;
  border: 1.6px solid #4386A9;
  display: inline-block;
}

.uploadedimage {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 30px;
}

.contents {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
}

.labelbtn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1.5rem;
  color: #ffff;
  background: #62A1EB;
  width: 100%;
  min-height: 40px;
  border-radius: 8px;
  cursor: pointer;
}

.submit {
  color: #fff;
  float: right;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 176px;
  height: 30px;
  background: #62A1EB;
  cursor: pointer;
  border: none;
}

.verybottom{
  display: flex;
  justify-content: space-between;
 
}

.submitbutton input {
  border-radius: 8px;
}